function App() {
  const [count, setCount] = useState(0);
  const [data, setData] = useState([]);
  const [showSearchAndCounting, setShowSearchAndCounting] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      const fetchedData = await fetchFirestoreData();
      setData(fetchedData);
      const filteredCount = fetchedData.filter(item => item.upload === 1).length;
      setCount(filteredCount);
    };
    fetchData();
  }, []);
  
  useEffect(() => {
    const fetchData = async () => {
      const fetchedData = await fetchFirestoreData();
      setData(fetchedData);
      const filteredCount = fetchedData.filter(item => item.upload == 1).length;
      setCount(filteredCount);
    };
    fetchData();
  }, []);


  return (
    <Router>
      <div>
        <div>
          <meta charSet="utf-8" />
          <title>Vic</title>
          <meta name="viewport" content="width=390, minimal-ui, viewport-fit=cover" />
          <meta property="og:image"
            content="assets/images/ComponentImages/Frame1.png" />
          <meta property="og:title" content="  " />
          <meta name="color-scheme" content="light only" />
          <meta name="supported-color-schemes" content="light" />
          <link rel="stylesheet" as="style" crossorigin
            href="https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.8/dist/web/static/pretendard.css" />
        </div>

        <div className='top-title'>
          {/* ... (previous code remains unchanged) */}
          <Link to="/" className='top' style={{ textDecoration: 'none', color: 'black' }}>Vic</Link>
          {showSearchAndCounting && (
            <Link to="/upload" className='upload-button' style={{ textDecoration: 'none' }}> </Link>
          )}
          {/* ... (previous code remains unchanged) */}
        </div>
        {showSearchAndCounting && (
          <div>
            <div className="search">
              <form className="search_box" action="" method="GET">
                <img src={process.env.PUBLIC_URL + '/public_assets/search_lens_image.png'} />
                <input type="text" />
              </form>
            </div>
            <div className="counting">
              <div className="count_all_text"></div>
              <div className="count">{count}</div>
            </div>
          </div>
        )}
        <Routes>
          <Route
            exact
            path="/"
            element={<Home data={data} setShowSearchAndCounting={setShowSearchAndCounting} />}
          />
          <Route path="/information/:id" element={<Information data={data} setShowSearchAndCounting={setShowSearchAndCounting} />} />
          <Route path="/upload" element={<Input setShowSearchAndCounting={setShowSearchAndCounting} />} />
        </Routes>
      </div>
    </Router >
  );
}

function Home({ data, setShowSearchAndCounting }) {
  const navigate = useNavigate();
  const filteredData = data.filter(item => item.upload == 1);
  return (
    <div>
      {filteredData.map((item, index) => (
        <div key={index} onClick={() => navigate(`/information/${item.id}`)}>
          <InformationButton data={item} />
        </div>
      ))}
    </div>
  );
}

function Input({ setShowSearchAndCounting }) {
  useEffect(() => {
    setShowSearchAndCounting(false);
    return () => setShowSearchAndCounting(true);
  }, [setShowSearchAndCounting]);

  const navigate = useNavigate();


  const [title, setTitle] = useState('');
  const [company_name, setcompany_name] = useState('');
  const [contact_information, setcontact_information] = useState('');
  const [location, setlocation] = useState('');
  const [employment_deadline, setemployment_deadline] = useState('');
  const [salary_system, setsalary_system] = useState('');
  const [salary_amount, setsalary_amount] = useState('');
  const [work_period, setwork_period] = useState('');
  const [week, setweek] = useState([]);
  const [work_hours, setwork_hours] = useState('');
  const [job_description, setjob_description] = useState('');
  const [qualifications, setqualifications] = useState('');
  const [preferred_qualifications, setpreferred_qualifications] = useState('');
  const [application_method, setapplication_method] = useState('');
  const [career, setcareer] = useState('');


  const handleCheckboxChange = (e) => {
    const { value } = e.target;
    if (week.includes(value)) {
      setweek(week.filter((day) => day !== value));
    } else {
      setweek([...week, value]);
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    const requiredFields = [
      { field: title, name: '' },
      { field: company_name, name: '' },
      { field: contact_information, name: '' },
      { field: location, name: '' },
      { field: employment_deadline, name: '  ' },
      { field: salary_amount, name: '' },
      { field: work_hours, name: '' },
      { field: career, name: '' }
    ];




    const emptyFields = requiredFields.filter(field => field.field === '');

    if (emptyFields.length > 0) {
      const emptyFieldNames = emptyFields.map(field => field.name);
      alert(`   : ${emptyFieldNames.join(', ')}`);
      return;
    }


    try {
      const docRef = await addDoc(collection(db, "Tabel"), {
        title: title,
        company_name: company_name,
        contact_information: contact_information,
        location: location,
        employment_deadline: employment_deadline,
        salary_system: salary_system,
        salary_amount: salary_amount,
        work_period: work_period,
        week: week.join(', '),
        work_hours: work_hours,
        job_description: job_description,
        qualifications: qualifications,
        preferred_qualifications: preferred_qualifications,
        application_method: application_method,
        career: career,
        upload: 2
      });
      console.log("Document written with ID: ", docRef.id);

      setTitle('');
      setcompany_name('');
      setcontact_information('');
      setlocation('');
      setemployment_deadline('');
      setsalary_system('');
      setsalary_amount('');
      setwork_period('');
      setweek([]);
      setwork_hours('');
      setjob_description('');
      setqualifications('');
      setpreferred_qualifications('');
      setapplication_method('');
      setcareer('');

      alert(" !    .");
      navigate('/');
      window.location.reload();
    } catch (e) {
      console.error("Error adding document: ", e);
      // Show error notification
      alert(" .  .");
    }
  };

  const handleSalaryAmountChange = (e) => {
    const input = e.target.value;
    const num = parseInt(input.replace(/[^0-9]/g, ''), 10);
    if (!isNaN(num)) {
      const formattedNum = num.toLocaleString();
      setsalary_amount(formattedNum);
    } else {
      setsalary_amount('');
    }
  };

  const addWonToSalaryAmount = () => {
    if (salary_amount) {
      return salary_amount + "";
    }
    return "";
  };


  const regex = /^[0-9:~]*$/;

  const handleTimeChange = (e) => {
    const { value } = e.target;
    if (regex.test(value)) {
      setwork_hours(value);
    }
  };

  return (
    <div>
      <div className='upload'>
        <div className='upload-title-first'></div>
        <input
          type="text"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          placeholder="  "
          id='upload-content'
        />
        <div className='upload-title'></div>
        <input
          type="text"
          value={company_name}
          onChange={(e) => setcompany_name(e.target.value)}
          placeholder=" "
          id='upload-content'
        />
        <div className='upload-title'></div>
        <input
          type="text"
          value={contact_information}
          onChange={(e) => setcontact_information(e.target.value)}
          placeholder=" "
          id='upload-content'
        />
        <div className='upload-title'></div>
        <input
          type="text"
          value={location}
          onChange={(e) => setlocation(e.target.value)}
          placeholder=" "
          id='upload-content'
        />
        <div className='upload-title'>  </div>
        <input
          type="date"
          value={employment_deadline}
          onChange={(e) => setemployment_deadline(e.target.value)}
          id='upload-content'
        />

        <div className='upload-title'></div>
        <select
          className='upload-content'
          value={salary_system} //  : setsalary_system  salary_system 
          onChange={(e) => setsalary_system(e.target.value)}
          placeholder=''
        >
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
        <input
          type="text"
          id='upload-content-2'
          value={addWonToSalaryAmount()}
          onChange={handleSalaryAmountChange}
        />


        <div className='upload-title'></div>
        <select
          className='upload-content'
          value={work_period}
          onChange={(e) => setwork_period(e.target.value)}
        >
          <option value=""></option>
          <option value="3 ">3 </option>
          <option value="3~6">3~6</option>
          <option value="6~1">6~1</option>
          <option value="1">1</option>
        </select>

        <div className='checkbox-group'>
          <label><input type="checkbox" name="days" value="" onChange={handleCheckboxChange} /></label>
          <label><input type="checkbox" name="days" value="" onChange={handleCheckboxChange} /></label>
          <label><input type="checkbox" name="days" value="" onChange={handleCheckboxChange} /></label>
          <label><input type="checkbox" name="days" value="" onChange={handleCheckboxChange} /></label>
          <label><input type="checkbox" name="days" value="" onChange={handleCheckboxChange} /></label>
          <label><input type="checkbox" name="days" value="" onChange={handleCheckboxChange} /></label>
          <label><input type="checkbox" name="days" value="" onChange={handleCheckboxChange} /></label>
          <label className='none-label'></label>
        </div>

        <div className='upload-title'></div>
        <input
          type="text"
          id='upload-content'
          value={work_hours}
          onChange={handleTimeChange}
          placeholder=") 08:30~18:30"
          id='upload-content'
        />
        <div className='upload-title'></div>
        <input
          type="text"
          value={career}
          onChange={(e) => setcareer(e.target.value)}
          placeholder="   "
          id='upload-content'
        />
        <div className='upload-title'></div>
        <input
          id='upload-design'
          type="text"
          value={job_description}
          onChange={(e) => setjob_description(e.target.value)}
          placeholder=" "
          id='upload-content'
        />
        <div className='upload-title'></div>
        <input
          type="text"
          value={application_method}
          onChange={(e) => setapplication_method(e.target.value)}
          placeholder=" "
          id='upload-content'
        />
        <div className='upload-title'></div>
        <input
          type="text"
          value={qualifications}
          onChange={(e) => setqualifications(e.target.value)}
          placeholder=" "
          id='upload-content'
        />
        <div className='upload-title'></div>
        <input
          type="text"
          value={preferred_qualifications}
          onChange={(e) => setpreferred_qualifications(e.target.value)}
          placeholder=" "
          id='upload-content'
        />

        <button className='submit-button' type="submit" onClick={handleSubmit}></button>

      </div>
    </div>
  )
}

